input{
    margin: 10px;
    height: 25px;
    font-size: 14px;
    border-bottom: 1.3px solid grey;
    border-top: none;
    border-left: none;
    border-right: none;
    width: 200px;
    background-color: transparent;
}

.inputBox{
    width: 90%;
    border: 1px solid grey;
    margin: 10px auto;
}

.textarea{
    margin: 10px auto 5px auto;
    height: 140px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.inputContent {
    overflow: hidden;
    word-wrap: break-word;
    resize: none;
    width: 50%;
    padding: 10px;
    border: none;
    overflow: scroll;
}

.display{
    width: 50%;
    padding: 10px;
    text-align: left;
    border-left: 1px solid grey;
    overflow: scroll;
    font-size: 12px;
}

.caption{
    font-size: 8px;
    text-align: right;
    margin: 2px 10px;
}

.inputBox p{
    margin-top: 0px;
}

.wordsCount{
    font-size: 8px;
    position: absolute;
    bottom: 30px;
    right: 8%;
    z-index: 50;
}




