.headline{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 400px;
    -ms-flex-align: center;
        align-items: center;
}

.headline img{
    width: 80px;
    margin: auto auto auto 15%;
}

.headline div{
    margin-right: 15%;
    width: 40%;
}

@media(min-width: 800px){
    .headline{
        display: -ms-flexbox;
        display: flex;
        width: 50%;
        height: 100%;
    }
}




