.toolbar{
    position: fixed;
    height: 48px;
    width: 100%;
    background-color: darkslategrey;
    font-family: FZpixes;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    z-index: 1000;
}

.toolbar p{
    margin: 8px;
    font-size: 32px;
}

.toolbar img{
    height: 32px;
    width: 32px;
    max-height: 100%;
    max-width: 100%;
    margin: 8px;
    cursor: pointer;
}


